import { Component, Injectable, OnInit, NgModule } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { platformBrowserDynamic} from '@angular/platform-browser-dynamic';
import { ReactiveFormsModule, FormControl, FormsModule} from '@angular/forms';
import { AssetsDataObject } from '../../Models/assets.class';
import { FilterDataObject } from '../../Models/filter.class';
import { DevicesDataObject, DevicesObject } from '../../Models/devices.class';
import { ApiService } from '../../shared/services/api.service';
import { DataService } from '../../shared/services/data-service.service';
import { FilterPipe } from '../../shared/services/filter-pipe';
import { HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/min';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [routerTransition()]
})
export class HomeComponent implements OnInit {
  pageTitle = 'Clima Control';
  data: any = null;
  searchableList: string[] = null;
  tbToken: any = null;
  loading = false;
  tbUser: string;
  assetData: AssetsDataObject[];
  deviceData: DevicesDataObject[];
  errorMessage;
  assetId;
  numberOfDevices: number;
  limit: number;
  page = 1;
  filter: FilterDataObject;


  constructor(
    private title: Title,
    private http: Http,
    private apiService: ApiService,
    private router: Router,
    private _dataService: DataService
  ) {
    this.searchableList = ['name'];
   }

  ngOnInit() {
    this.loading = true;
    if (JSON.parse(localStorage.getItem('currentUser')) === null) {
    this.apiService.tbLogin()
    .subscribe(response => {
      if (response === true) {   // login successful
        const tbUser = JSON.parse(localStorage.getItem('currentUser'));
        this.tbToken = tbUser.token;
        console.log('the tbToken is : ===>', this.tbToken);
    }
    },
      (error) => {this.errorMessage = error; this.loading = false; },
      () => {this.loading = false; this.getAssets(); }
    );
      } else {
        this.getAssets();
      }
  }

  login() {
    this.loading = true;
    this.apiService.tbLogin()
    .subscribe(response => {
      if (response === true) {   // login successful
        const tbUser = JSON.parse(localStorage.getItem('currentUser'));
        this.tbToken = tbUser.token;
        console.log('the tbToken is : ==========================>', this.tbToken);
    }
    },
      (error) => {this.errorMessage = error; this.loading = false; },
      () => {this.loading = false; }
     );
    }

    getAssets() {
      this.loading = true;
      this.errorMessage = '';
      const tbUser = JSON.parse(localStorage.getItem('currentUser'));
      this.tbToken = tbUser.token;
      this.apiService.getAssets(this.tbToken)
          .subscribe((data) => {
              this.assetData = data;
              console.log('asssetdataobject test : ==========================>', this.assetData);
          },
          (error) => {this.errorMessage = error; this.loading = false; },
          () => {this.loading = false;
          }
        );
    }
    getDevices() {
      this.loading = true;
      this.errorMessage = '';
      const tbUser = JSON.parse(localStorage.getItem('currentUser'));
      this.tbToken = tbUser.token;
      this.apiService.getDevices(this.tbToken)
          .subscribe((data) => {
              this.deviceData = data;
              this.numberOfDevices = this.deviceData.length;
              this.limit = this.deviceData.length;
              console.log('device data object is: ==========================>', this.deviceData);
          },
          (error) => {this.errorMessage = error; this.loading = false; },
          () => {this.loading = false;
          }
        );
    }
    storeAssetId(id) {
      this._dataService.assetId = id;
    }



}



